a{
  color:orange
}
.newContainer {
  padding: 1em 6.5em 1em 6.5em;
  h2 {
    color: gold;
  }
}
.title,
.date,
.content,
.autor {
  margin: 0;
}
.date,
.autor {
  color: rgb(255 255 255 / 76%);
}
.title {
  font-size: 3.8em;
}
.subtitle {
  font-size: 2.7em;
  margin: 1% 0%;
}
.subtitle-2 {
  font-size: 2.7em;
  margin: 10% 0% 2.5% 5%;
}
.content {
  font-size: 1.8em;
  line-height: 1.5;
}
.autorContainer {
  display: flex;
  align-items: center;
  margin-bottom: 1em;
  img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    background: unset;
    margin-left: 0.5em;
  }
  figure {
    width: 1.5em;
    height: 1.5em;
  }
}
